html,
body,
#root {
  height: 100%;
}

html,body{
  background-color: #dbdde0;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  margin: 0;
  padding: 0;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

a {
  color: #4CD0FF;
  -webkit-text-decoration-color: rgba(0, 210, 233, 0.5);
          text-decoration-color: rgba(0, 210, 233, 0.5);
}

.container{
    position: fixed;
    width: 100%;
    background:#fff;
    font-size: 22px;
    z-index: 1;
    box-shadow:
    0px 2px 4px -1px rgba(0,0,0,0.2), 
    0px 4px 5px 0px rgba(0,0,0,0.14), 
    0px 1px 10px 0px rgba(0,0,0,0.1) inset;
}

.header{
    padding: 20px 40px;
}
.title{
    cursor: pointer;
    display: inline-block;
    padding-top: 5px;
    transition: all 0.3s ease;
    color: #000;
    text-decoration: none;
}

.title:hover{
    color: #0857d3;
}

.nav{
    font-size: 16px;
    list-style: none;
    text-transform: uppercase;
    display: inline-block;
    float: right;
    margin: 0;
    margin-bottom: 15px;
}

.nav-item{
    display: inline-block;
    margin-right:30px;
    padding: 10px;
    border-radius:10px;
    transition: all 0.5s ease; 
    color: #000;
    text-decoration: none;
}

.nav-item:hover{
    cursor: pointer;
    background: #dbdde0;
     
}
.login-form{
    position: fixed;
    width: 300px;
    top:50%;
    left:50%;
    -webkit-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
    background: #fff;
    padding: 20px;
    border-radius:5px;
    box-shadow:
     0px 2px 4px -1px rgba(0,0,0,0.2), 
    0px 4px 5px 0px rgba(0,0,0,0.14), 
    0px 1px 10px 1px rgba(0,0,0,0.1) inset;
}

.login-form-title{
   text-align: center;
   margin: 0;
   font-size: 24px;
}


.login-form-input{
    margin-top: 20px;
    margin-bottom: 25px;
    font-size: 18px;
    border: none;
    border-bottom: 1px solid #000;
    background-color: #fff !important;
    outline: none;

}

.login-form-input:focus{
    border-bottom: 1px solid #6c96d8;
}

.login-form-btn{
    padding: 10px 15px;
    margin-top: 10px;
    background: #fff;
    border: 1px solid #6c96d8;
    outline:none;
    text-transform:uppercase;
    font-size: 18px;
    transition: all 0.3s ease;
}

.login-form-btn:hover{
    background: #6c96d8;
    color:#fff;
    cursor:pointer;
}

.login-form-error{
    margin: 0;
    color:#e01c18;
}

.mapBox{
    position: relative;
    top: 75px;
    left: 0;
    width: 100%;
    height: 100%;
}
